import { defineComponent } from "vue";
import AdminLayout from "@/layouts/Admin/index.vue";
import "vue-toast-notification/dist/theme-sugar.css";
import QRCode from "easyqrcodejs";
import { v4 as uuidv4 } from "uuid";

const redirectUrl = `${process.env.VUE_APP_BASE_URL}/int/slack`;

export default defineComponent({
  name: "Integrations",
  components: {
    AdminLayout,
  },
  data() {
    return {
      context: process.env.VUE_APP_CONTEXT,
      redirectUrl,
      telegramQRCode: "",
      integrationSelected: "slack",
      telegramIntegrationLink: "",
      qrcode: null as any,
      telegramIntegrationType: "private",
    };
  },
  created() {
    const actionOutcome = this.$route.params.actionOutcome;
    if (actionOutcome) {
      const messageType = actionOutcome === "error" ? "error" : "success";
      this.showToast(messageType, this.$route.params.actionMessage.toString());
    }
  },
  mounted() {
    this.generateQRCode("private");
  },
  methods: {
    generateQRCode(type: string) {
      if (this.qrcode) this.qrcode.clear();
      const uid = uuidv4();
      const privateUrl = `https://t.me/boto_io_bot?start=${uid}`;
      const groupUrl = `https://t.me/boto_io_bot?startgroup=${uid}`;
      const text = type === "private" ? privateUrl : groupUrl;
      const options = {
        text,
        logo: `${this.context}img/boto_icon_800.png`,
        colorDark: "#20B083",
        colorLight: "#d4fadf",
        width: 400,
        height: 400,
      };
      this.telegramIntegrationLink = text;
      // Create new QRCode Object
      this.qrcode = new QRCode(this.$refs.qrcode, options);
    },
    select(integration: string) {
      this.integrationSelected = integration;
    },
    showToast(type: string, message: string) {
      this.$toast.open({
        message,
        type,
        duration: 5000,
        dismissible: true,
        position: "top-right",
      });
    },
  },
});
